.body {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  border: 1px solid #105276;
  display: block;
}

.is-hidden {
  opacity: 0;
  display: block;
}

.cat-info {
  background-color: #9585ae;
  border: 1px solid #e6dde4;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
  padding: 20px;
  display: flex;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 16px;
  height: 16px;
  background: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, 50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

